button,h1{position:relative}img,p{position:absolute}button,p{transform:translate(-50%,0)}h1,h3,h4{text-align:center}button,h1,h3,h4{animation-name:fade;animation-duration:5s}*{margin:0;padding:0;font-family:Preahvihear,sans-serif;color:#00f}body{background-color:#87ceeb;height:100vh;overflow-y:hidden}img{width:100%;z-index:100;animation-name:start;animation-duration:2.5s;top:100%;transform:translate(0,-100%)}@keyframes start{0%{top:200%}100%{top:100%}}h1{margin:50px;font-size:500%;color:#006400}@keyframes fade{0%,50%{opacity:0}100%{opacity:1}}button{left:50%;padding:5px 15px;font-size:larger;z-index:101}button:hover{cursor:pointer}p{z-index:5;font-size:xx-large;top:90%}@keyframes drop{0%{top:0}100%{top:90%}}h3,h4{margin-top:20px;font-size:80px}h4{font-size:60px}